<template>
  <div class="review-detail">
    <el-form
      label-position="left"
      label-width="100px"
      :model="consulting"
      inline
    >
      <el-form-item label="点评人" class="search-item">
        <el-select
          v-model="consulting.reviewer"
          clearable
          filterable
          placeholder="请选择"
        >
          <el-option
            v-for="item in reviewerList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="分数" class="search-item">
        <el-select v-model="consulting.score" clearable placeholder="请选择">
          <el-option
            v-for="item in scoreList"
            :key="item.title"
            :label="item.title"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="select-btn" @click="select"
          >查询</el-button
        >
      </el-form-item>
    </el-form>
    <div class="list">
      <div class="title">
        <div>
          {{
            $route.query.date ? parseInt($route.query.date.split('-')[1]) : ''
          }}月点评详情
        </div>
      </div>
      <el-table :data="list" style="width: 100%" :empty-text="hasListInfo">
        <el-table-column
          fixed
          prop="reviewTime"
          label="点评时间"
          min-width="100"
          align="center"
        />
        <el-table-column
          prop="reviewer"
          label="被点评人"
          min-width="120"
          align="center"
        />
        <el-table-column
          prop="reviewPeo"
          label="点评人"
          min-width="120"
          align="center"
        />
        <el-table-column
          prop="score"
          label="分数"
          min-width="120"
          align="center"
        />
        <el-table-column fixed="right" label="操作" width="150" align="center">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="examine(scope.row)"
              >查看</el-button
            >
            <el-button
              type="text"
              size="small"
              :style="{ color: scope.row.type === 'use' ? '' : '#f56c6c' }"
              @click="deprecated(scope.row)"
              >{{ scope.row.type === 'use' ? '弃用' : '已弃用' }}</el-button
            >
            <span
              v-if="
                !scope.row.handler_at &&
                  type === 'human_resource' &&
                  scope.row.feedback
              "
              class="tip-spot"
            />
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          :current-page="currentPage"
          :page-sizes="[12, 18, 24]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { getReviewList, setUseStatus, commentor } from '@/api/learningAdmin'
export default {
  data() {
    return {
      consulting: {
        reviewer: '',
        score: [0, 100]
      },
      reviewerList: [],
      scoreList: [
        { title: '全部', value: [0, 100] },
        { title: '91-100', value: [91, 100] },
        { title: '81-90', value: [81, 90] },
        { title: '71-80', value: [71, 80] },
        { title: '61-70', value: [61, 70] },
        { title: '60以下', value: [0, 60] }
      ],
      hasListInfo: '暂无数据',
      list: [],
      currentPage: 1,
      pageSize: 12,
      count: 0,
      type: this.$store.state.user.mts_user
        ? this.$store.state.user.mts_user.type
        : ''
    }
  },
  computed: {
    getTime() {
      return function(time) {
        const year = new Date(time).getFullYear()
        let month = new Date(time).getMonth() + 1
        const date = new Date(time).getDate()
        if (month < 10) month = '0' + month
        return `${year}-${month}-${date}`
      }
    }
  },
  created() {
    this.commentor()
    this.getReviewList()
  },
  methods: {
    async select() {
      this.currentPage = 1
      await this.getReviewList(this.consulting)
      if (!this.list.length) this.hasListInfo = '暂无数据，请重新输入关键词'
      else this.hasListInfo = '暂无数据'
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getReviewList(this.consulting)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getReviewList(this.consulting)
    },
    examine(row) {
      this.$router.push('/learning-admin/comment/see-reviews/' + row.id)
    },
    async deprecated(row) {
      await setUseStatus(row.id)
      this.getReviewList(this.consulting)
    },
    async getReviewList(search) {
      const params = {
        page: this.currentPage,
        pageSize: this.pageSize,
        month: this.$route.query.date,
        commentator: this.$route.params.id
      }
      if (search) {
        if (search.reviewer) params.reviewer = search.reviewer
        if (search.score) params.start_score = search.score[0]
        if (search.score) params.end_score = search.score[1]
      }
      const { data, code } = await getReviewList(params)
      if (code === 20000) {
        this.count = data.count
        if (data.list.length) {
          this.list = data.list.map(item => {
            return {
              reviewTime: this.getTime(item.created_at),
              reviewer: item.commentator.name,
              reviewPeo: item.reviewer ? item.reviewer.name : '/',
              score: item.score,
              id: item._id,
              type: item.type,
              handler_at: item.handler_at,
              feedback: item.feed_back_at
            }
          })
        } else this.list = data.list
      }
    },
    async commentor() {
      const { code, data } = await commentor()
      if (code === 20000) {
        const list = data.filter(v => {
          return v.bus_role === 'doctor' && v.section_office === 'pediatric'
        })
        this.reviewerList = list.map(item => {
          return {
            value: item._id,
            label: item.name
          }
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.review-detail {
  .search-item {
    margin-right: 70px;
    color: #17233d;
    /deep/input {
      width: 220px;
      height: 32px;
    }
    /deep/span {
      top: -3px;
    }
    /deep/.el-input__suffix {
      top: 0;
    }
  }
  .select-btn {
    padding: 0 20px;
    width: 68px;
    height: 32px;
  }
  .pagination {
    margin-top: 50px;
    text-align: center;
  }
  .list {
    margin: 8px 0;
    padding: 20px;
    width: 100%;
    border: 1px solid #ebeef5;
    .title {
      padding-bottom: 30px;
      font-size: 20px;
      font-weight: bold;
      color: #333;
    }
  }
  /deep/.el-divider--horizontal {
    margin: 8px 0 30px;
  }
  .tip-spot {
    position: relative;
    top: -5px;
    left: -36px;
    display: inline-block;
    width: 3px !important;
    height: 3px;
    border: 2px solid #f00;
    border-radius: 50%;
  }
}
</style>
